.video-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: var(--background-color);
  padding: 20px;
}

.video-container {
  background-color: var(--white);
  padding: 20px;
  box-shadow: 0 0 10px var(--shadow);
  border-radius: 8px;
  width: 90%;
  max-width: 1200px;
  text-align: center;
}

.video-page-title {
  font-size: 24px;
  margin-bottom: 20px;
  text-align: left;
}

.video-player {
  margin-top: 20px;
}

.video-player video {
  width: 100%;
  height: auto;
}

.video-main-content {
  display: flex;
  flex-direction: row; /* This makes the content flow horizontally */
  justify-content: space-between; /* Ensures space is distributed between items */
  align-items: flex-start; /* Aligns the items at the top */
}

.video-screen-content {
  flex: 5; /* Adjust the flex ratio to take more space compared to the recommended videos */
  padding: 20px;
  background-color: var(--white);
  box-shadow: 0 0 10px var(--shadow);
  border-radius: 8px;
}

.recommended-videos {
  flex: 1.4; /* Make the recommended videos section take less space */
  margin-left: 20px; /* Adds some spacing between video content and recommended videos */
  display: flex;
  flex-direction: column; /* Ensure videos stack vertically */
  background-color: var(--white);
  border-radius: 8px;
  box-shadow: 0 0 10px var(--shadow);
  overflow-y: auto; /* Enable vertical scrolling */
  max-height: 1000px;
  }

.recommended-title {
  color: var(--title-color);
  font-weight: bold;
  padding: 20px;
  margin-top: 30px;
  font-size: 18px;
}