.account-edit-user-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--dark);
  z-index: 1000;
}

.account-edit-user-box {
  width: 900px;
  padding: 30px;
  background-color: var(--white);
  box-shadow: 0 0 10px var(--shadow);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.account-close-button {
  position: absolute;
  top: 20px;
  right: 20px;
  background: none;
  border: none;
  font-size: 30px;
  cursor: pointer;
}

.account-edit-user-title {
  font-size: 24px;
  color: var(--title-color);
  margin-bottom: 20px;
  width: 100%;
  text-align: center;
}

.account-edit-user-form {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.account-left-section {
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.account-right-section {
  width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.account-left-section {
  margin-right: 20px;
}

.account-right-section {
  margin-left: 20px;
}

.account-upload-input-group {
  width: 100%;
  margin-bottom: 20px;
  text-align: left;
}

.account-upload-input-group label {
  display: block;
  margin-bottom: 10px;
  font-size: 15px;
  color: var(--secondary-text-color);
}

.account-upload-input-group input {
  width: 100%;
  height: 25px;
  padding: 10px;
  font-size: 14px;
  border: 1px solid var(--border);
  border-radius: 4px;
  color: var(--black);
  background-color: var(--white);
}

.button-group {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.account-upload-button {
  width: 100px;
  padding: 10px;
  background-color: #d70202;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 20px;
}

.account-upload-button:hover {
  background-color: var(--deeper-red);
}

.account-delete-button {
  width: 100px;
  padding: 10px;
  background-color: var(--red);
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 20px;
}

.account-delete-button:hover {
  background-color: var(--deeper-red);
}

.account-photo-box {
  width: 200px;
  height: 200px;
  border: 1px solid var(--border);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--background-color);
  margin-top: 10px;
  margin-left: 20px;
}

.account-photo-preview {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.account-upload-link {
  color: var(--red);
  text-decoration: underline;
  cursor: pointer;
  display: block;
  text-align: center;
  margin: 10px 0;
}

.account-upload-link:hover {
  color: var(--deeper-red);
}

input[type='file'] {
  display: none;
}
