.profile-details-horizontal {
  display: flex;
  align-items: flex-start; /* Align items to the top */
  padding: 20px;
  background-color: var(--white);
  border-radius: 8px;
  margin: 10px;
}

.profile-picture-container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center align items */
}

.profile-text {
  margin-left: 20px;
  text-align: left;
}

.name {
  font-size: 24px;
  font-weight: bold;
  color: var(--black);
  margin: 10px 0;
}

.displayname {
  font-size: 20px;
  color: var(--primary-text-color);
  margin: 8px 0;
}

.email {
  font-size: 18px;
  color: var(--secondary-text-color);
  margin: 6px 0;
}

.account-link {
  font-size: 15px;
  text-decoration: underline;
  color: var(--red);
  margin-top: 10px;
  text-align: center;
}

.account-link:hover {
  text-decoration: underline;
  cursor: pointer;
}

.profile-pic-big {
  width: 120px;
  height: 120px;
}
